<template>
  <a-modal
    :maskClosable="false"
    :title="title"
    :visible="!!modalShow"
    @cancel="cancel"
    @ok="ok"
    class="add-hyx-customer-modal"
    width="800px"
  >
    <div style="display: flex; height: 450px">
      <!--  -->
      <div style="width: 50%; display: flex; flex-direction: column">
        <!--  -->
        <div style="padding-bottom: 20px">
          <a-input-group compact style="margin-bottom: 15px">
            <a-select v-model="searchSelect" style="width: 30%">
              <a-select-option value="custName">  </a-select-option>
              <a-select-option value="businessName">  </a-select-option>
              <a-select-option value="contacts">  </a-select-option>
              <a-select-option value="phone">  </a-select-option>
            </a-select>
            <a-input style="width: 70%" v-model="searchInput" />
          </a-input-group>
          <template v-for="(item, index) in Object.keys(searchObj)">
            <a-input-group compact :key="index" style="margin-bottom: 15px">
              <a-input
                style="width: 30%; cursor: default"
                :value="getName(item)"
                readOnly
              />
              <a-range-picker
                style="width: 70%"
                v-model="searchObj[item]"
                v-if="
                  item === 'inputTime' ||
                  item === 'actionTime' ||
                  item === 'nextActionTime'
                "
              />
              <!--  -->
              <a-select
                style="width: 70%"
                v-model="searchObj[item]"
                v-else-if="item === 'resGroup'"
              >
                <a-select-opt-group
                  v-for="(groupItem, groupIndex) in queryResource[item]"
                  :key="groupIndex"
                >
                  <span slot="label"
                    ><a-icon type="user" />{{ groupItem.text }}</span
                  >
                  <a-select-option
                    :value="groupItemItem.id"
                    v-for="(
                      groupItemItem, groupIndexIndex
                    ) in groupItem.children"
                    :key="groupIndexIndex"
                  >
                    {{ groupItemItem.text }}
                  </a-select-option>
                </a-select-opt-group>
              </a-select>
              <!--  -->
              <a-select
                style="width: 70%"
                v-model="searchObj[item]"
                v-else-if="item === 'custPortrait'"
                :mode="'multiple'"
                :maxTagCount="2"
              >
                <a-select-opt-group
                  v-for="(groupItem, groupIndex) in queryResource[item]"
                  :key="groupIndex"
                >
                  <span slot="label"
                    ><a-icon type="user" />{{ groupItem.groupName }}</span
                  >
                  <a-select-option
                    :value="groupItemItem.id"
                    v-for="(
                      groupItemItem, groupIndexIndex
                    ) in groupItem.childList"
                    :key="groupIndexIndex"
                  >
                    {{ groupItemItem.optionName }}
                  </a-select-option>
                </a-select-opt-group>
              </a-select>
              <!--  -->
              <a-tree-select
                v-else-if="item === 'inputGroup'"
                style="width: 70%"
                v-model="searchObj[item]"
                :tree-data="queryResource[item]"
                search-placeholder=""
                :replaceFields="{
                  children: 'children',
                  title: 'text',
                  key: 'id',
                  value: 'id',
                }"
              />
              <!--  -->
              <a-select
                v-else-if="item === 'resSource'"
                style="width: 70%"
                v-model="searchObj[item]"
              >
                <a-select-option
                  v-for="(groupItem, groupIndex) in queryResource[item]"
                  :value="groupItem.value"
                  :key="groupIndex"
                >
                  {{ groupItem.name }}
                </a-select-option>
              </a-select>
            </a-input-group>
          </template>
          <div
            style="
              display: flex;
              align-items: center;
              justify-content: space-between;
            "
          >
            <a @click="clickSet"></a>
            <a-button
              type="primary"
              icon="search"
              style="width: 50px"
              @click="search"
            />
          </div>
        </div>
        <!--  -->
        <div
          style="
            border-top: 1px solid #ccc;
            flex: 1;
            overflow: hidden;
            display: flex;
            flex-direction: column;
          "
        >
          <div style="padding: 15px 0"></div>
          <div style="overflow: auto">
            <div
              v-for="(item, index) in selectedList"
              :key="index"
              style="margin-bottom: 10px;border;1px solid #ccc;background:#f7f7f7;padding: 5px 10px;display:flex;justify-content:space-between;align-items:center"
            >
              <Tooltip
                v-if="
                  item.custName.length +
                    (item.contacts || '').length +
                    (item.phone || '').length >
                  22
                "
                :str="`${item.custName} - ${item.contacts} - ${item.phone}`"
                :max="22"
              />
              <span v-else>
                {{ item.custName }} - {{ item.contacts }}
                {{ item.contacts ? "-" : "" }} {{ item.phone }}</span
              >
              <a-icon
                type="close"
                style="cursor: pointer"
                @click="deleteCustomer(item)"
              />
            </div>
          </div>
        </div>
      </div>
      <!--  -->
      <div style="width: 50%; padding: 0 20px; overflowY: auto;overflowX: hidden;">
        <a-spin :spinning="searchLoading" style="width: 100%">
          <a-checkbox
            v-for="(item, index) in dataList"
            :checked="isChecked(item)"
            :key="index"
            @change="
              (e) => {
                checkChange(e, item);
              }
            "
            :disabled="!item.phone"
          >
            <div class="checkbox-item">
              <div class="checkbox-item-top">
                <div>
                  <span class="title"></span>
                  {{ item.custName }}
                </div>
                <div>
                  <span class="title"></span>
                  <span style="margin-right: 20px">
                    <Tooltip :str="item.contacts" :max="4" />
                  </span>
                  <span class="title"></span>
                  <span>{{ item.phone }}</span>
                </div>
              </div>
              <div class="checkbox-item-bottom">
                <div
                  v-for="(listItem, listIndex) in item.business"
                  :key="listIndex"
                >
                  {{ listIndex + 1 }}. {{ listItem.name }}
                </div>
              </div>
            </div>
          </a-checkbox>
        </a-spin>
      </div>
    </div>
    <a-modal
      :maskClosable="false"
      title=""
      v-if="!!setModalShow"
      :visible="!!setModalShow"
      @cancel="setCancel"
      @ok="setOk"
    >
      <a-row>
        <a-checkbox-group :value="setModalShow" @change="changeSet">
          <a-col
            v-for="(item, index) in queryItemList"
            :key="index"
            span="8"
            style="margin-bottom: 10px"
          >
            <a-checkbox :value="item.key">{{ item.name }}</a-checkbox>
          </a-col>
        </a-checkbox-group>
      </a-row>
      <div style="position: absolute; bottom: 15px; right: 165px">
        {{ setModalShow.length }} / 3
      </div>
    </a-modal>
  </a-modal>
</template>

<script>
import Tooltip from "@/components/Tooltip.vue";
import api from "../../../api/api";
import axios from "axios";
import moment from "moment";
export default {
  name: "addHyxCustomerModal",
  props: {
    modalShow: {
      type: [String, Boolean],
      default: false,
      required: false,
    },
    title: {
      type: String,
      default: "",
      required: false,
    },
  },
  components: {
    Tooltip,
  },
  data() {
    return {
      moment,
      searchSelect: "custName", // 
      searchInput: "", // 
      searchObj: {}, // 
      setModalShow: false, // 
      queryItemList: [
        { name: "", key: "resGroup" },
        { name: "", key: "resSource" },
        { name: "/", key: "inputTime" },
        { name: "", key: "actionTime" },
        { name: "", key: "nextActionTime" },
        { name: "", key: "custPortrait" },
        { name: "", key: "inputGroup" },
      ], // 
      queryResource: {
        resGroup: [], // 
        resSource: [], // 
        custPortrait: [], // 
        inputGroup: [], //  
      }, // 
      searchLoading: false,
      dataList: [], // 
      selectedList: [], // 
    };
  },
  created() {
    this.search();
    this.getQueryResource();
  },
  methods: {
    // s
    getQueryResource() {
      axios
        .all([
          api.orgGroupGet_group_json(), // 
          api.taoCustomerGetCustPortraitList(), // 
          api.resGroupGet_res_group_json(), // 
          api.addcustSearchs(), // resSource
        ])
        .then((res) => {
          console.log(
            "",
            res[0],
            "",
            res[1],
            "",
            res[2],
            "resSource",
            res[3]
          );
          this.queryResource = {
            resGroup: res[2].data, // 
            resSource: res[3].data.resSource, // 
            custPortrait: res[1].data, // 
            inputGroup: res[0], //  
          }; // 
        });
    },
    // 
    clickSet() {
      this.setModalShow = Object.keys(this.searchObj) || [];
    },
    // checkbox
    changeSet(value) {
      console.log(value);
      if (value.length > 3) {
        this.$message.error("3");
      } else {
        this.setModalShow = value;
      }
    },
    // key
    getName(key) {
      const targetObj = this.queryItemList.find((item) => {
        return item.key === key;
      });
      return targetObj.name;
    },
    // ok
    setOk() {
      let newObj = {};
      this.setModalShow.forEach((item) => {
        if (
          item === "resGroup" ||
          item === "resSource" ||
          item === "inputGroup"
        ) {
          newObj[item] = null;
        } else {
          newObj[item] = [];
        }
      });
      this.searchObj = newObj;
      this.setModalShow = false;
      console.log(this.searchObj);
      // this.searchObj = {
      //   resGroup: null,
      //   resSource: null,
      //   inputTime: [],
      //   actionTime: [],
      //   nextActionTime: [],
      //   custPortrait: [],
      //   inputGroup: null,
      // };
    },
    // cancel
    setCancel() {
      this.setModalShow = false;
    },
    // 
    search() {
      // this.searchObj = {
      //   resGroup: null,
      //   resSource: null,
      //   inputTime: [],
      //   actionTime: [],
      //   nextActionTime: [],
      //   custPortrait: [],
      //   inputGroup: null,
      // };
      let searchObj = {};
      Object.keys(this.searchObj).forEach((item) => {
        if (this.searchObj[item] instanceof Array) {
          if (item === "custPortrait") {
            searchObj[item] = this.searchObj[item].join(",")
              ? this.searchObj[item].join(",")
              : null;
          } else {
            searchObj[item] = this.searchObj[item].join(",")
              ? `${moment(this.searchObj[item][0]).format(
                  "YYYY-MM-DD"
                )},${moment(this.searchObj[item][1]).format("YYYY-MM-DD")}`
              : null;
          }
        } else {
          searchObj[item] = this.searchObj[item];
        }
      });
      console.log(
        "",
        this.searchSelect,
        this.searchInput,
        this.searchObj,
        searchObj
      );
      this.searchLoading = true;
      api
        .addcustList({
          queryType: this.searchSelect,
          queryText: this.searchInput,
          ...searchObj,
        })
        .then((res) => {
          console.log(res);
          this.dataList = res.data;
          // this.dataList = [
          //   {
          //     custId: 1,
          //     custName: "",
          //     contacts: "",
          //     phone: "13989898989",
          //     business: [
          //       { name: "" },
          //       { name: "asdsdsadsa" },
          //       { name: "IP" },
          //     ],
          //   },
          //   {
          //     custId: 2,
          //     custName: "fff",
          //     contacts: "s",
          //     phone: "13967898989",
          //     business: [{ name: "111" }, { name: "asdsdsadsa" }],
          //   },
          // ];
          this.searchLoading = false;
        })
        .catch((e) => {
          this.searchLoading = false;
        });
    },
    // checkbox
    isChecked(targetObj) {
      let flag = false;
      this.selectedList.forEach((item) => {
        if (item.custId === targetObj.custId) {
          flag = true;
        }
      });
      return flag;
    },
    // checkboxchange
    checkChange(e, targetObj) {
      console.log(e.target.checked, targetObj);
      let isExist = false;
      this.selectedList.forEach((item) => {
        if (item.custId === targetObj.custId) {
          isExist = true;
        }
      });
      if (isExist) {
        this.selectedList = this.selectedList.filter((item) => {
          return item.custId !== targetObj.custId;
        });
      } else {
        this.selectedList.push(targetObj);
      }
    },
    // 
    deleteCustomer(targetObj) {
      this.selectedList = this.selectedList.filter((item) => {
        return item.custId !== targetObj.custId;
      });
    },
    // 
    ok() {
      console.log("okokokokok");
      const ids = this.selectedList.map((item) => {
        return item.custId;
      });
      if (this.selectedList.length > 0) {
        this.$emit("ok", ids, this.selectedList);
      } else {
        this.$message.error("");
      }
    },
    // 
    cancel() {
      console.log("cancelcancel!!!!");
      this.$emit("cancel");
    },
  },
};
</script>

<style lang="less">
.add-hyx-customer-modal {
  .ant-checkbox-wrapper {
    display: flex;
    align-items: center;
    margin: 0;
    margin-bottom: 15px;
    .checkbox-item {
      padding: 10px;
      border: 1px solid #ccc;
      width: 310px;
      .checkbox-item-top {
        padding-bottom: 10px;
        border-bottom: 1px solid #ccc;
        .title {
          color: #b6b6b6;
        }
      }
      .checkbox-item-bottom {
        padding-top: 10px;
      }
    }
  }
}
</style>
